export default {
	data() {
		return {

		}
	},
	methods: {
		login() {
			if (!this.userInfo) {
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}
		}
	},
	computed: {
		userInfo() {
			return this.$store.state.userInfo
		},
		id() {
			return this.userInfo ? `id:  ${this.userInfo._id.slice(-8)}` : '未登陆'
		},
		gender() {
			if (this.userInfo) {
				switch (this.userInfo.gender) {
					case 1:
						return '/static/images/boy.png'
					case 2:
						return '/static/images/girl.png'
				}
			}
		}
	}
}
